#result {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, #d16ba5, #d670ae, #da75b7, #de7ac0, #e27fc9, #d58ddf, #c39bf1, #afa8ff, #76bfff, #20d2ff, #00e2ff, #35ede5);    display: flex;
    justify-content: center;
    align-items: center;
}

.gameoverbox {
    background-color: white;
    padding: 100px  0 0 0;
    width: 40%;
    height: 15%;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: relative;
}

.gameoverbox button {
    background-color: transparent;
    position: absolute;
    top: 0px;
    right: 0px;
    outline: none;
}

#gameover {
    font-size: 24px;
    color: rgb(106, 107, 107);
    font-family: 'Turret Road', cursive;

}