@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Turret+Road&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#d16ba5), color-stop(#c777b9), color-stop(#ba83ca), color-stop(#aa8fd8), color-stop(#9a9ae1), color-stop(#8aa7ec), color-stop(#79b3f4), color-stop(#69bff8), color-stop(#52cffe), color-stop(#41dfff), color-stop(#46eefa), to(#5ffbf1));
  background-image: -webkit-linear-gradient(top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
  background-image: linear-gradient(to bottom, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
}

h1 {
  margin: 0;
  padding: 20px;
  font-family: 'Pacifico', cursive; 
  font-size: 100px;
}




.circle {
    height: 200px;
    width: 200px;
    border-radius: 100px;
    text-align: center;
    display: inline-block;
    margin: 5px;
    box-shadow: 0 0 6px 2px rgb(252, 250, 250);
    background: white;    
}


button {
    margin: 30px;
    border-radius: 5px;
    padding: 10px;
    background-color: rgb(252, 250, 247);
    font-family: 'Turret Road', cursive;
    font-size: 20px;
}

.circle.active {
    box-shadow: inset 0px 0px 15px rgb(204, 202, 202);
    background: rgb(240, 241, 240);
}

#result {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#d16ba5), color-stop(#d670ae), color-stop(#da75b7), color-stop(#de7ac0), color-stop(#e27fc9), color-stop(#d58ddf), color-stop(#c39bf1), color-stop(#afa8ff), color-stop(#76bfff), color-stop(#20d2ff), color-stop(#00e2ff), to(#35ede5));
    background-image: -webkit-linear-gradient(bottom, #d16ba5, #d670ae, #da75b7, #de7ac0, #e27fc9, #d58ddf, #c39bf1, #afa8ff, #76bfff, #20d2ff, #00e2ff, #35ede5);
    background-image: linear-gradient(to top, #d16ba5, #d670ae, #da75b7, #de7ac0, #e27fc9, #d58ddf, #c39bf1, #afa8ff, #76bfff, #20d2ff, #00e2ff, #35ede5);    display: flex;
    justify-content: center;
    align-items: center;
}

.gameoverbox {
    background-color: white;
    padding: 100px  0 0 0;
    width: 40%;
    height: 15%;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    position: relative;
}

.gameoverbox button {
    background-color: transparent;
    position: absolute;
    top: 0px;
    right: 0px;
    outline: none;
}

#gameover {
    font-size: 24px;
    color: rgb(106, 107, 107);
    font-family: 'Turret Road', cursive;

}
